    this.useMount -= this.rangeValue / 100;
    return this.useMount.toFixed(5);
  }

  created() {
    this.getSumMount();
  }

  mounted() {
    this.currrentTab = this.$route.params.type === 'buy' ? '买入' : '卖出';
  }

