    );
  }
  changeNowPrice1() {
    this.showSheetName = '限价';
    this.sheetVisible = false;
  }
  changeNowPrice2() {
    this.showSheetName = '市价';
    this.sheetVisible = false;
  }
  showNowPrice() {
    this.sheetVisible = true;
  }
  getRangeValue(obj: any) {
